import {PublicKey, Transaction, TransactionInstruction} from "@solana/web3.js";
import BigInteger from "big-integer";
import {parseMetadata, parseOffer} from "./parsers";
import {getAssociatedAccount, getDeckAddress, getDeckTreasury, getMetadataAddress, getOfferAddress} from "./accounts";
import env from "../env";
import {NFT} from "@/methods/types";


// ++++++++++ Internal helpers ++++++++++
const zero = BigInteger(0);
const one = BigInteger(1);
const n256 = BigInteger(256);

function fromLittleEndian(bytes) {
    let result = zero;
    let base = one;
    bytes.forEach(function (byte) {
        result = result.add(base.multiply(BigInteger(byte)));
        base = base.multiply(n256);
    });
    return result;
}

function toLittleEndian(bigNumber, length) {
    let result = new Uint8Array(length);
    let i = 0;
    while (bigNumber.greater(zero)) {
        result[i] = bigNumber.mod(n256);
        bigNumber = bigNumber.divide(n256);
        i += 1;
    }
    return result;
}

const SystemProgramId = new PublicKey("11111111111111111111111111111111");
const TokenProgram = new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA");
const AssociatedProgram = new PublicKey("ATokenGPvbdGVxr1b2hvZbsiqW5xWH25efTNsLJA8knL");
const MetadataProgram = new PublicKey("metaqbxxUerdq28cj1RbAWkYQm3ybzjb6a8bt518x1s");
const Sysvar = new PublicKey("SysvarRent111111111111111111111111111111111");


export async function makeOffer(nft: NFT, wallet: PublicKey, price: BigInteger){
    const deck = await getDeckAddress(nft.collectionAddress);
    const offer = await getOfferAddress(nft.mint);
    const metadata = await getMetadataAddress(nft.mint);
    const treasury = await getDeckTreasury(nft.mint, deck);
    let incrIx = new TransactionInstruction({
        keys: [
            {
                pubkey: wallet,
                isSigner: true,
                isWritable: true,
            },
            {
                pubkey: deck,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: offer,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: nft.mint,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: treasury,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: nft.account,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: metadata,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: new PublicKey("11111111111111111111111111111111"),
                isSigner: false,
                isWritable: false,
            },
            {
                pubkey: new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA"),
                isSigner: false,
                isWritable: false,
            },
            {
                pubkey: new PublicKey("ATokenGPvbdGVxr1b2hvZbsiqW5xWH25efTNsLJA8knL"),
                isSigner: false,
                isWritable: false,
            },
            {
                pubkey: new PublicKey("SysvarRent111111111111111111111111111111111"),
                isSigner: false,
                isWritable: false,
            }
        ],
        programId: env.MarketplaceProgram,
        data: Buffer.from(new Uint8Array([1].concat(Array.from(toLittleEndian(price, 8))))),
    });
    return new Transaction().add(incrIx);
}

export async function cancelOffer(offerAddress: PublicKey, wallet: PublicKey) {
    const offer = await parseOffer(offerAddress);
    const treasury = await getDeckTreasury(offer.nft_mint, offer.deck);
    const nft_account = await getAssociatedAccount(wallet, offer.nft_mint);
    let incrIx = new TransactionInstruction({
        keys: [
            {
                pubkey: wallet,
                isSigner: true,
                isWritable: true,
            },
            {
                pubkey: offer.deck,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: offerAddress,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: treasury,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: nft_account,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA"),
                isSigner: false,
                isWritable: false,
            }
        ],
        programId: env.MarketplaceProgram,
        data: Buffer.from(new Uint8Array([2])),
    });
    return new Transaction().add(incrIx);
}

export async function acceptOffer(offerAddress: PublicKey, wallet: PublicKey) {
    const offer = await parseOffer(offerAddress);
    const meta = await parseMetadata(await getMetadataAddress(offer.nft_mint));
    let royaltiesReceivers = [];
    for(let i = 0; i < meta.data.creators.length; i++){
        royaltiesReceivers.push({
            pubkey: meta.data.creators[i].address,
            isSigner: false,
            isWritable: true,
        });
    }
    let incrIx = new TransactionInstruction({
        keys: [
            {
                pubkey: wallet,
                isSigner: true,
                isWritable: true,
            },
            {
                pubkey: offer.seller,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: offer.deck,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: offerAddress,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: offer.nft_mint,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: await getDeckTreasury(offer.nft_mint, offer.deck),
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: await getAssociatedAccount(wallet, offer.nft_mint),
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: await getMetadataAddress(offer.nft_mint),
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: env.FeeTreasury,
                isSigner: false,
                isWritable: true,
            },
            {
                pubkey: new PublicKey("11111111111111111111111111111111"),
                isSigner: false,
                isWritable: false,
            },
            {
                pubkey: new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA"),
                isSigner: false,
                isWritable: false,
            },
            {
                pubkey: new PublicKey("ATokenGPvbdGVxr1b2hvZbsiqW5xWH25efTNsLJA8knL"),
                isSigner: false,
                isWritable: false,
            },
            {
                pubkey: new PublicKey("SysvarRent111111111111111111111111111111111"),
                isSigner: false,
                isWritable: false,
            }
        ].concat(royaltiesReceivers),
        programId: env.MarketplaceProgram,
        data: Buffer.from(new Uint8Array([3])),
    });
    return new Transaction().add(incrIx);
}