<template>
  <div type="button" v-on:click="redirect"
      class="col col-sm-6 col-md-3 col-8 m-auto p-2 p-xl-4"
      :style="get('GS.recentRaffles', []).length > index? '':'visibility: hidden;'">
    <div class="card p-0 rounded-3 overflow-hidden grey-border">
      <img :src="get('GS.recentRaffles['+index+'].image')" class="card-img-top"/>
      <div class="card-body text-center p-0">
        <h5 class="card-title white-letter m-0">{{get('GS.recentRaffles['+index+'].deck_name')}}</h5>
        <p class="card-text dark-blue-letter m-0">Live</p>
      </div>
    </div>
  </div>
</template>

<script>
import {defineComponent} from "vue";
import {Page} from "@/methods/types";
export default defineComponent({
  name: "RaffleCard",
  props: ['index'],
  methods: {
    redirect(){
      global.App.setPage(Page.Raffle, this.get('GS.recentRaffles['+this.index+'].deck_name'));
    }
  },
  data() {
    return {
      get: global.get
    }
  },
  beforeMount() {
    global.updateList.add(this);
  },
  beforeUnmount() {
    global.updateList.delete(this);
  }
});
</script>
