
import {defineComponent} from "vue";
import NavBar from "@/components/NavBar.vue";
import GlobalInstance from "@/methods/global/global-instance";
import LocalMemory from "@/methods/local-memory";
import IdlePage from "@/components/Idle/IdlePage.vue";
import {Page} from "@/methods/types";
import CollectionPage from "@/components/Collection/CollectionPage.vue";
import SearchPage from "@/components/Search/SearchPage.vue";
import Wallet from "@/methods/wallet";
import PocketPage from "@/components/Pocket/PocketPage.vue";
import CollectionLoad from "@/components/Collection/CollectionLoad.vue";
export default defineComponent({
  name: 'App',
  components: {
    CollectionLoad,
    PocketPage,
    SearchPage,
    CollectionPage,
    IdlePage,
    NavBar
  },
  methods: {
    async setPage(page: Page, ...args) {
      switch (page){
        case Page.Collection:
          this.activePage = Page.CollectionLoad;
          await this.GI.switchDeck(args[0]);
          LocalMemory.setActiveDeck(args[0]);
          break;
        default: {
          await this.GI.switchDeck(undefined);
        }
      }
      LocalMemory.setPage(page);
      this.activePage = page;
    }
  },
  beforeMount() {
    this.GI = new GlobalInstance(this);
    this.wallet = new Wallet();
    this.setPage(LocalMemory.getPage(), LocalMemory.getActiveDeck()); // Not awaited
  },
  data() {
    return {
      GI: undefined as GlobalInstance,
      wallet: undefined as Wallet,
      activePage: Page.CollectionLoad as Page,
      Page: Page
    }
  }
});
