<template>
  <div class="col-12 col-md-6 col-lg-3 p-1" v-if="offer">
    <div class="card p-0 rounded-3 overflow-hidden" :class="activeCard===offer.address? 'blue-border': 'grey-border'"
         v-on:click="() => {setChosen(offer.address, true)}">
      <img :src="offer.image" class="card-img-top img"/>
      <div class="card-body row p-0 px-2">
        <div class="card-text grey-letter">{{offer.name}}</div>
        <div class="card-text grey-letter text-start">{{display.sol(offer.price)}}</div>
      </div>
    </div>
  </div>
  <div class="col-12 col-md-6 col-lg-3 p-1" v-else>
    <div class="card p-0 rounded-3 overflow-hidden" :class="activeCard===nft.mint.toBase58()? 'blue-border': 'grey-border'"
         v-on:click="() => {setChosen(nft.mint.toBase58(), false)}">
      <img :src="nft.image" class="card-img-top img"/>
      <div class="card-body row p-0 px-2">
        <div class="col card-text grey-letter">{{nft.name}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import {defineComponent} from "vue";
import display from "@/methods/display";
export default defineComponent({
  name: "NFTCard",
  props: ['offer', 'nft', 'setChosen', 'activeCard'],
  data() {
    return {
      display: display,
    }
  },
});
</script>