/* eslint-disable */
import {PublicKey, TransactionResponse} from "@solana/web3.js";
import env from "../../env";
import BigInteger from "big-integer";
import {toLittleEndian} from "@/methods/transaction-verifier/big-byte";
import {parseMetadata, parseOffer} from "@/methods/transaction-verifier/parsers";
import {getMetadataAddress} from "@/methods/transaction-verifier/accounts";
import { base58_to_binary } from "base58-js";


function extractString(s: string): string { //@ts-ignore
    return s.replace(/\0/g, '');
}

interface ParsedTransaction {
    programId: PublicKey,
    keys: Array<{
        pubkey: PublicKey
    }>,
    data: Uint8Array
}

function rawToParsed(txRaw: TransactionResponse): ParsedTransaction {
    let keys: Array<{pubkey: PublicKey}> = [];
    txRaw.transaction.message.instructions[0].accounts.forEach((accountId) => {
        keys.push({
            pubkey: txRaw.transaction.message.accountKeys[accountId]
        });
    });
    let tx = {
        programId: txRaw.transaction.message.accountKeys[txRaw.transaction.message.instructions[0].programIdIndex],
        data: base58_to_binary(txRaw.transaction.message.instructions[0].data),
        keys: keys
    };
    return tx;
}

function parseTx(func: Function): Function {
    return async (txId: string, ...args) => {
        try {
            const txRaw = await env.connection.getTransaction(txId, {commitment: "confirmed"});
            const tx: ParsedTransaction = rawToParsed(txRaw);
            if (txRaw.transaction.message.instructions.length !== 1) return false;
            if (!tx.programId.equals(env.MarketplaceProgram)) return false;
            console.log("Transaction Checked:", txId)
            return await func(tx, ...args);
        } catch (_e) {
            //console.log(_e);
            return false;
        }
    }
}

async function checkCancelTx(tx: ParsedTransaction, offerAddress: string){
    if(tx.data[0]!==2) return false;
    if(!tx.keys[2].pubkey.equals(new PublicKey(offerAddress))) return false;
    return true;
}

async function checkAcceptTx(tx: ParsedTransaction, buyer: string, offerAddress: string) {
    if(tx.data[0]!==3) return false;
    if(!tx.keys[0].pubkey.equals(new PublicKey(buyer))) return false;
    if(!tx.keys[3].pubkey.equals(new PublicKey(offerAddress))) return false;
    return true;
}

async function checkPlaceTx(tx: ParsedTransaction, seller: string, offerAddress: string, deckAddress: string, price: string, name: string, symbol: string) {
    if(tx.data[0]!==1) return false;
    if(!tx.keys[0].pubkey.equals(new PublicKey(seller))) return false;
    if(!tx.keys[1].pubkey.equals(new PublicKey(deckAddress))) return false;
    if(!tx.keys[2].pubkey.equals(new PublicKey(offerAddress))) return false; //@ts-ignore
    const idxArray = toLittleEndian(BigInteger(price), 8);
    if(JSON.stringify(Array.from(tx.data))!==JSON.stringify([1].concat(Array.from(idxArray)))) return false;
    const offer = await parseOffer(new PublicKey(offerAddress));
    const meta = await parseMetadata(await getMetadataAddress(offer.nft_mint));
    if(extractString(meta.data.name)!==name) return false;
    if(extractString(meta.data.symbol)!==symbol) return false;
    return true;
}

