/* eslint-disable */
import {io} from "socket.io-client";
import env from "../env";
import {
    Error,
    ErrorToString,
    MessageIn,
    MessageOut,
    OfferUpdate,
    OfferUpdateType
} from "@/methods/types";
import LocalMemory from "@/methods/local-memory";
import Tx from "../methods/transaction-verifier/transaction-parser";
import {VerifyAnswer, VerifyRequest, VerifyResponse, VerifyType} from "@/methods/transaction-verifier/types";


let SI: Socket = undefined; // SocketInstance

class Methods {
    async getGlobal() {
        return await SI.makeRequest('GetGlobal', []);
    }
    async getDeck(name: string) {
        return await SI.makeRequest('GetDeck', [name]);
    }
    async getUserOffers(user: string) {
        return await SI.makeRequest('GetUserOffers', [user]);
    }
    async getPastOffers(user: string) {
        return await SI.makeRequest('GetPastOffers', [user]);
    }
    async createOffer(seller: string, deckName: string, offerAddress: string,
                      nftName: string, nftImage: string, price: string, tx: string) {
        return await SI.makeRequest('CreateOffer', [seller, deckName, offerAddress, nftName, nftImage, price], tx);
    }
    async cancelOffer(offerAddress: string, tx: string){
        return await SI.makeRequest('CancelOffer', [offerAddress], tx);
    }
    async acceptOffer(buyer: string, offerAddress: string, tx: string) {
        return await SI.makeRequest('AcceptOffer', [buyer, offerAddress], tx);
    }
    async insertCollection(name: string, image: string, collection: string, deck: string, supply: string){
        return await SI.makeRequest('InsertCollection', [name, image, collection, deck, supply]);
    }
    async createRaffle(deckName: string, address: string, startDate: string, price: string, supply: string){
        return await SI.makeRequest('CreateRaffle', [deckName, address, startDate, price, supply]);
    }
    async approveRaffle(address: string) {
        return await SI.makeRequest('approveRaffle', [address]);
    }
    async raffleTickets(address: string){
        return await SI.makeRequest('RaffleTickets', [address]);
    }
    async subscribe(collections: string[]){
        return await SI.makeRequest('Subscribe', collections);
    }
    async unSubscribe(collections: string[]){
        return await SI.makeRequest('UnSubscribe', collections);
    }
}
const signedMethods = new Set(["GetUserOffers", "GetPastOffers", 'CreateOffer', 'CancelOffer', 'AcceptOffer']); // Need verification part of MessageIn

export default class Socket{
    methods;
    socket;
    resolvers: Map<number, Function> = new Map();
    index: number = 0;
    constructor() {
        SI = this;
        this.methods = new Methods();
        this.socket = io(env.SocketURL, { transports: ['websocket', 'polling', 'flashsocket'] });
        this.socket.on('update', this.onUpdate);
        this.socket.on('response', this.onResponse);
        this.socket.on('verify_request', this.onVerify);
    }
    async makeRequest(method: string, params: string[], tx?: string|undefined) {
        let msgIn: MessageIn;
        if(signedMethods.has(method)){
            const w = LocalMemory.getWallet();
            msgIn = {
                id: this.index,
                method,
                params,
                verification: {
                    key: w.key.toBase58(), //@ts-ignore
                    signature: w.signature.data
                },
                tx: tx
            }
        }
        else{
            msgIn = {
                id: this.index,
                method,
                params,
                verification: undefined,
                tx: tx
            }
        }
        const resolver = new Promise((resolve, reject) => {
            this.resolvers.set(this.index, resolve);
            // TODO setTimeout for request
        });
        this.index++;
        this.socket.emit('call', msgIn);
        const msgOut = await resolver as MessageOut;
        if(msgOut.status != Error.Ok) {
            console.log('Request failed with:', ErrorToString(msgOut.status));
            return undefined;
        }
        else {
            if(msgOut==undefined) return true;
            return msgOut.result;
        }
    }
    onResponse(args: MessageOut) {
        const id = args.id;
        const resolver = SI.resolvers.get(id);
        if(!resolver){
            console.log("Undefined Resolver");
            return;
        }
        SI.resolvers.delete(id);
        resolver(args);
    }
    onUpdate(args: OfferUpdate) {
        if(!args){
            console.log("Invalid update");
        }
        switch (args.type){
            case OfferUpdateType.Created:
                global.GI.pushOfferCreated(args.offerAddress, args.deckName, args.offer);
                break;
            case OfferUpdateType.Canceled:
                global.GI.pushOfferCanceled(args.offerAddress, args.deckName);
                break;
            case OfferUpdateType.Accepted:
                global.GI.pushOfferAccepted(args.offerAddress, args.deckName);
                break;
        }
    }
    async onVerify(args: VerifyRequest) {
        try {
            let res: boolean = false;
            switch(args.type){
                case VerifyType.OfferAccepted:
                    res = await Tx.checkAccept(args.tx, ...args.args);
                    break;
                case VerifyType.OfferCanceled:
                    res = await Tx.checkCancel(args.tx, ...args.args);
                    break;
                case VerifyType.OfferPlaced:
                    res = await Tx.checkPlace(args.tx, ...args.args);
                    break;
            }
            SI.socket.emit('verify_response', {
                tx: args.tx,
                answer: res? VerifyAnswer.True: VerifyAnswer.False
            } as VerifyResponse);
        }
        catch (_e){
            //console.log(_e);
            SI.socket.emit('verify_response', {
                tx: args.tx,
                answer: VerifyAnswer.Rejected
            } as VerifyResponse);
        }
    }
}