/* eslint-disable */
import {PublicKey} from "@solana/web3.js";
import {acceptOffer, cancelOffer, makeOffer} from "@/methods/transactions";
import env from "../env";
import signer from 'nacl-signature';
import LocalMemory from "@/methods/local-memory";
import {showError, showSuccess} from "./alert";


export default class Wallet {
    publicKey: PublicKey|undefined = undefined;
    provider = undefined;
    methods: {};
    constructor() {
        global.wallet = this;
        this._onLoad();
        this.methods = {
            makeOffer: this._methodWrapper(makeOffer),
            cancelOffer: this._methodWrapper(cancelOffer),
            acceptOffer: this._methodWrapper(acceptOffer)
        }
    }
    async getProvider() {
        if ("solana" in window) { //@ts-ignore
            await window.solana.connect() //@ts-ignore
            this.provider = window.solana;
            const signature = await this._signVerification();
            if(signature){
                LocalMemory.setWallet({signature, key: this.provider.publicKey});
                this.publicKey = this.provider.publicKey;
                global.GI.updatePocket();
            }
        }
    }
    _constructMessage(key: string): string {
        return "Marketplace wants to sign you these message to verify wallet: "+key+'.';
    }
    async _signVerification(): Promise<string|undefined>{
        try{//@ts-ignore
            const msg = this._constructMessage(this.provider.publicKey.toBase58());
            const encodedMessage = new TextEncoder().encode(msg);
            const signature = await this.provider.signMessage(encodedMessage, "utf8");
            if (signer.verify(msg, Buffer.from(signature.signature).toString('base64'), this.provider.publicKey.toBuffer().toString('base64'))){
                return signature.signature;
            }
            else{
                return undefined;
            }
        }
        catch (_e){
            return undefined;
        }
    }
    async _onLoad(){
        const w = LocalMemory.getWallet();
        if(!w) return;
        const msg = this._constructMessage(w.key.toBase58());
        if (!signer.verify(msg, Buffer.from(w.signature).toString('base64'), w.key.toBuffer().toString('base64'))) return;
        this.publicKey = w.key;
        global.GI.restorePocket();
        global.GI.updatePocket();
    }
    async _checkProvider(): Promise<boolean>{
        if(!this.publicKey){
            showError("Connect Wallet first!");
            return false
        }
        if(!this.provider) {
            if (!("solana" in window)) {
                showError("Wallet not found!");
                return false;
            } //@ts-ignore
            await window.solana.connect(); //@ts-ignore
            this.provider = window.solana;
        }
        if(!this.provider.publicKey.equals(this.publicKey)){
            showError("Wallet not match!");
            return false;
        }
        return true;
    }
    _methodWrapper(func: Function){
        return async (...args) => {try{
            const tx = await func(...args);
            if(!(await this._checkProvider())) return;
            tx.recentBlockhash = (await env.connection.getRecentBlockhash()).blockhash;
            tx.feePayer = this.publicKey;
            const txId = await this.provider.signAndSendTransaction(tx);
            console.log(txId.signature);
            await env.connection.confirmTransaction(txId.signature);
            //console.log(r)
            //console.log(await env.connection.getTransaction(txId.signature, {commitment: "confirmed"})); throw "Proxy";
            showSuccess("Transaction Succeed!");
            return txId.signature;
        }
        catch (e){
            console.log(e);
            showError("Transaction failed");
            return undefined;
        }}
    }
}