
import {defineComponent} from "vue";
import NFTCard from "@/components/Pocket/NFTCard.vue";
import SaleRow from "@/components/Pocket/SaleRow.vue";
import display from "@/methods/display";
import {PublicKey} from "@solana/web3.js";
export default defineComponent({
  name: "PocketPage",
  methods: {
    checkFilter(bPlaced: boolean){
      // At least one should be checked
      if(bPlaced) {
        this.bShowPlaced ^= 1;
        this.bShowOwned = true;
      }
      else{
        this.bShowOwned ^= 1;
        this.bShowPlaced = true;
      }
      //document.getElementById("placedCheckbox").checked = true;
      //document.getElementById("ownedCheckbox").checked = true;
    },
    setChosen(address: string, bOffer: boolean) {
      this.chosenCard = address;
      this.bOffer = bOffer;
    },
    async cancelOffer(){
      const txId = await global.wallet.methods.cancelOffer(new PublicKey(this.chosenCard), global.wallet.publicKey);
      if(txId){
        const address = this.chosenCard;
        this.chosenCard = undefined;
        await global.GI.offerCanceled(this.get('pocket.placed').get(address), txId);
      }
    },
    async makeOffer() {
      const nft = global.GI.pocket.owned.get(this.chosenCard);
      const amount = display.convertFloat(this.price, 9);
      if(!amount) {
        console.log("Cannot parse float");
        return;
      }
      const txId = await global.wallet.methods.makeOffer(nft, global.wallet.publicKey, amount);
      if(txId){
        this.chosenCard = undefined;
        await global.GI.offerPlaced(nft, amount, txId);
      }
    }
  },
  components: {SaleRow, NFTCard},
  data() {
    return {
      get: global.get,
      display: display,
      chosenCard: undefined,
      bOffer: false,
      price: "vdv" as string,
      bShowPlaced: true,
      bShowOwned: true
    }
  },
  beforeMount() {
    global.updateList.add(this);
  },
  beforeUnmount() {
    global.updateList.delete(this);
  }
});
