/* eslint-disable */
import {PublicKey} from "@solana/web3.js";
import env from "@/env";
import {Metadata} from "@metaplex-foundation/mpl-token-metadata";
import {fromLittleEndian} from "./big-byte";


export async function parseOffer(record: PublicKey) {
    try {
        // @ts-ignore
        const data = (await env.connection.getAccountInfo(record)).data;
        return {
            tag: data.slice(0, 1)[0],
            deck: new PublicKey(data.slice(1, 33)),
            seller: new PublicKey(data.slice(33, 65)),
            nft_mint: new PublicKey(data.slice(65, 97)),
            price: fromLittleEndian(data.slice(97, 105)),
            fulfilled: data.slice(105, 106)[0]!==0,
        }
    } catch (e) {
        console.log(e)
        return undefined;
    }
}

export async function parseMetadata(record: PublicKey | string): Promise<Metadata | undefined> {
    try {
        if (typeof (record) === "string") {
            record = new PublicKey(record);
        }
        return await Metadata.fromAccountAddress(env.connection, record);
    } catch (e) {
        return undefined;
    }
}