
import {defineComponent} from "vue";
import display from "@/methods/display";
import {PublicKey} from "@solana/web3.js";
import {Offer} from "@/methods/types";
export default defineComponent({
  name: "OfferBoard",
  props: ['activeCard', 'setChosen'],
  methods: {
    checkWallet(){
      return global.wallet.publicKey===undefined;
    },
    calcFee(_price) {
      const price = parseInt(_price.toString());
      return Math.floor((price * 15) / 1000);
    },
    calcRoyalty(_price){
      const price = parseInt(_price.toString());
      return Math.floor((price * this.offer.cached.royalty) / 10000);
    },
    update(){
      this.offer = global.get('deckOffers', {activeCard: undefined}).get(this.activeCard)
    },
    async buy(){
      const txId = await global.wallet.methods.acceptOffer(new PublicKey(this.activeCard), global.wallet.publicKey);
      if(txId){
        const offer: Offer = this.get('deckOffers').get(this.activeCard);
        this.setChosen(undefined);
        await global.GI.offerAccepted(offer, txId);
      }
    }
  },
  watch: {
    activeCard: {
      deep: true,
      handler(){
        this.update()
      }
    }
  },
  data() {
    return {
      get: global.get,
      display: display,
      offer: undefined
    }
  },
  beforeMount() {
    global.updateList.add(this);
  },
  beforeUnmount() {
    global.updateList.delete(this);
  }
})
