/* eslint-disable */
import {NFT, Offer} from "@/methods/types";
import {PublicKey} from "@solana/web3.js";
import {Metadata} from "@metaplex-foundation/mpl-token-metadata";
const splToken = require("@solana/spl-token");
import axios from "axios";
import {getAssociatedAccount, getMetadataAddress} from "@/methods/accounts";
import env from "../env";
import BN from "bn.js";


export async function parseMetadata(record: PublicKey|string): Promise<Metadata|undefined>{
    try{
        if(typeof(record)==="string"){
            record  = new PublicKey(record);
        }
        return await Metadata.fromAccountAddress(env.connection, record);
    }
    catch(e){
        return undefined;
    }
}

async function fetchMetadata(uri: string){
    const metadata = await axios.get(uri)
        .then(function (response) {
            return response.data;
        });
    return metadata
}

export async function parseOffer(record: PublicKey) {
    try{
        // @ts-ignore
        const data = (await env.connection.getAccountInfo(record)).data;
        return{
            deck: new PublicKey(data.slice(1, 33)),
            seller: new PublicKey(data.slice(33, 65)),
            nft_mint: new PublicKey(data.slice(65, 97))
        }
    }
    catch(e){
        console.log(e)
        return undefined;
    }
}

export async function loadNFTCaches(offer: Offer): Promise<Offer> {
    const parsedOffer = await parseOffer(new PublicKey(offer.address));
    if(!parsedOffer) return;
    const meta = await getMetadataAddress(parsedOffer.nft_mint);
    const metadata = await parseMetadata(meta);
    const fetchedMeta = await fetchMetadata(metadata.data.uri); //@ts-ignore
    offer.cached = {
        traits: fetchedMeta.attributes,
        royalty: fetchedMeta.seller_fee_basis_points
    }
    return offer;
}

function extractString(s: string): string { //@ts-ignore
    return s.replace(/\0/g, '');
}

export async function searchNFTs(owner: PublicKey, collection: Set<string>): Promise<Map<string, NFT>>{
    const accounts = await env.connection.getParsedProgramAccounts(
        new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA"),
        {
            filters: [
                {
                    dataSize: 165,
                },
                {
                    memcmp: {
                        offset: 32, //@ts-ignore
                        bytes: owner,
                    },
                },
            ],
        }
    );
    const ownedNFTs: Map<string, NFT> = new Map();
    for(let i = 0; i < accounts.length; i++){
        try{//@ts-ignore
            const info = accounts[i].account.data.parsed.info;
            if(info.state!=="initialized") continue;
            if(info.tokenAmount.amount!=='1'&&info.tokenAmount.decimals!=='0') continue;
            const meta = await parseMetadata(await getMetadataAddress(new PublicKey(info.mint)));
            if(!meta) continue;
            if(!meta.collection) continue;
            if(!meta.collection.verified || !collection.has(meta.collection.key.toBase58())) continue;
            const url = await fetchMetadata(extractString(meta.data.uri));
            ownedNFTs.set(meta.mint.toBase58(), {
                mint: meta.mint,
                account: accounts[i].pubkey,
                collectionAddress: meta.collection.key,
                collectionName: global.GI.GS.collectionList[global.GI.GS.collectionAddress[meta.collection.key.toBase58()]],
                name: extractString(meta.data.name),
                image: url.image,
                cached: undefined
            });
        }
        catch (_e){}
    }
    return ownedNFTs;
}