/* eslint-disable */
import {IGlobalState, IPocket, Page} from "@/methods/types";
import {PublicKey} from "@solana/web3.js";


function mapToObject<V>(m: Map<string, V>): object {
    let o: object = {};
    m.forEach((value, key) => {
        o[key] = JSON.stringify(value);
    })
    return o;
}
function objectToMap<V>(o: object): Map<string, V> {
    const m = new Map();
    for(let key in o){
        m.set(key, JSON.parse(o[key]));
    }
    return m;
}

let LM = {
    setGS(gs: IGlobalState) {
        localStorage.setItem('globalState', JSON.stringify(gs));
    },
    loadGS(): IGlobalState {
        return JSON.parse(localStorage.getItem('globalState')) as IGlobalState;
    },
    loadPocket(): IPocket {
        const v = localStorage.getItem('pocketInstance');
        if(!v) return {placed: new Map(), owned: new Map(), past: []};
        const o = JSON.parse(v);
        const nfts = objectToMap(o.owned);
        nfts.forEach(nft => { // @ts-ignore
            nft.mint = new PublicKey(nft.mint); // @ts-ignore
            nft.account = new PublicKey(nft.account); // @ts-ignore
            nft.collectionAddress = new PublicKey(nft.collectionAddress);
        }); // @ts-ignore
        return {placed: objectToMap(o.placed), owned: nfts, past: JSON.parse(o.past)};
    },
    setPocket(pocket: IPocket){
        const s = {
            owned: mapToObject(pocket.owned),
            placed: mapToObject(pocket.placed),
            past: JSON.stringify(pocket.past)
        };
        localStorage.setItem('pocketInstance', JSON.stringify(s));
    },
    setWallet(w: {signature: string|undefined, key: PublicKey| undefined}){
        localStorage.setItem('walletSignature', JSON.stringify({
            signature: w.signature,
            key: w.key? w.key.toBase58(): undefined
        }));
    },
    getWallet(): {signature: string|undefined, key: PublicKey| undefined} {
        const w = localStorage.getItem('walletSignature');
        if(!w) return undefined;
        const ww = JSON.parse(w) as {signature: string, key: string};
        if(!ww) return undefined;
        if(!ww.key || !ww.signature) return undefined;
        return {signature: ww.signature, key: new PublicKey(ww.key)};
    },
    setPage(page: Page){
        localStorage.setItem('activePage', (page as number).toString());
    },
    getPage(): Page{
        const v = localStorage.getItem('activePage');
        if(!v) return Page.Idle;
        const p = parseInt(v) as Page;
        if(!p) return Page.Idle;
        return p;
    },
    setActiveDeck(name: string){
        localStorage.setItem('activeDeck', name);
    },
    getActiveDeck(): string|undefined{
        return localStorage.getItem('activeDeck');
    }
}


export default LM;
