<template>
  <section class="container" style="margin-top: 3rem;">
    <div class="row">
      <img :src="get('activeDeck.image')" class="col-6 col-md-3" style="width: 15rem; height: 15rem; border-radius: 7.5rem;"/>
      <div class="col-6 col-md-3 px-2">
        <h4 class="white-letter">{{get('activeDeck.name')}}</h4>
        <div class="grey-letter rounded-2 px-2 my-2" style="background-color: var(--light-black);">Floor: {{display.sol(get('activeDeck.floor', 0))}}</div>
        <div class="grey-letter rounded-2 px-2 my-2" style="background-color: var(--light-black);">Total Volume: {{display.sol(get('activeDeck.volume', 0))}}</div>
        <div class="grey-letter rounded-2 px-2 my-2" style="background-color: var(--light-black);">Supply: {{display.supply(get('activeDeck.supply', 0))}}</div>
      </div>
      <div class="grey-letter col-12 col-md-5 col-lg-6 py-2 py-md-0">
        <b>Description</b><br/>
        {{get('activeDeck.description')}}
      </div>
    </div>
  </section>
  <section class="container p-4">
    <div class="row">
      <div class="white-border col-12 col-md-6 col-xl-9 row g-0 p-2" style="">
        <OfferCard v-for="offer in get('deckOffers').values()" :key="offer"
                   :offer="offer" :chosen-card="chosenCard" :set-chosen="setChosen"/>
      </div>
      <div class="white-border col-0 col-md-6 col-xl-3" style="border-right: none; border-bottom: none; border-left: none;">
        <OfferBoard :active-card="chosenCard" :set-chosen="setChosen"/>
      </div>
    </div>
  </section>
</template>

<script>
import {defineComponent} from "vue";
import OfferCard from "@/components/Collection/OfferCard";
import OfferBoard from "@/components/Collection/OfferBoard";
import display from "@/methods/display";
export default defineComponent({
  name: "CollectionPage",
  components: {OfferBoard, OfferCard},
  methods: {
    async setChosen(address) {
      await global.GI.loadNFT(address);
      this.chosenCard = address;
    },
  },
  data() {
    return {
      get: global.get,
      display: display,
      chosenCard: undefined
    }
  },
  beforeMount() {
    global.updateList.add(this);
  },
  beforeUnmount() {
    global.updateList.delete(this);
  }
});
</script>

