// ++++++++++ Verify Requests ++++++++++
export enum VerifyType {
    OfferPlaced,
    OfferCanceled,
    OfferAccepted
}

export enum VerifyAnswer {
    False,
    True,
    Rejected,
    Missed
}

export interface VerifyRequest {
    type: VerifyType,
    tx: string,
    args: string[]
}

export interface VerifyResponse {
    tx: string,
    answer: VerifyAnswer
}