import {PublicKey} from "@solana/web3.js";

export enum Error {
    Ok,
    NotExist,
    AlreadyExist,
    CriticalError,
    InvalidMessage,
    MethodNotFound,
    VerificationFailed,
    InvalidTransaction
}

export function ErrorToString(e: Error) {
    switch (e){
        case Error.Ok:
            return 'Ok';
        case Error.NotExist:
            return 'NotExist';
        case Error.AlreadyExist:
            return 'AlreadyExist';
        case Error.CriticalError:
            return 'CriticalError';
        case Error.InvalidMessage:
            return 'InvalidMessage';
        case Error.MethodNotFound:
            return 'MethodNotFound';
        case Error.VerificationFailed:
            return 'VerificationFailed';
        case Error.InvalidTransaction:
            return 'InvalidTransaction';
    }
}

export interface IDeck {
    address: string,
    name: string,
    image: string,
    collection: string,
    supply: number,
    volume: number,
    floor: number,
    trade_volume: number,
    description: string
}

export interface IRaffle {
    address: string,
    deck_address: string,
    deck_name: string,
    collection: string,
    image: string,
    start: number,
    price: number,
    supply: number
}

export interface IOffer {
    address: string,
    deck_name: string,
    name: string,
    image: string,
    seller: string,
    price: string,
}

export enum OfferUpdateType {
    Created,
    Canceled,
    Accepted
}

export interface OfferUpdate {
    type: OfferUpdateType,
    offer: IOffer | undefined,
    deckName: string,
    offerAddress: string
}

export interface MessageIn {
    method: string,
    params: string[],
    id: number,
    verification: undefined|{
        key: string,
        signature: string
    },
    tx: string|undefined
}

export interface MessageOut {
    result: object | undefined,
    status: Error
    id: number
}

// ++++++++++ Website Specific ++++++++++
export interface IGlobalState {
    topCollections: IDeck[];
    collectionList: string[];
    collectionAddress: object;
    recentRaffles: IRaffle[];
}

export interface Trait {
    type: string,
    value: string
}

export interface CachedTraits {
    cached: undefined|{
        traits: Trait[],
        royalty: number
    }
}

export interface NFT extends CachedTraits{
    mint: PublicKey,
    account: PublicKey,
    collectionAddress: PublicKey,
    collectionName: string,
    name: string,
    image: string,
}

export interface Offer extends IOffer, CachedTraits{}

export interface PastOffer {
    deck_name: string,
    name: string,
    price: number,
    time: number
}

export interface IPocket {
    owned: Map<string, NFT>, // <nftMintAddress, NFT>
    placed: Map<string, Offer>, // <OfferAddress, Offer>
    past: PastOffer[]
}

export enum Page{
    Idle,
    Search,
    Collection,
    Pocket,
    Raffle,
    CollectionLoad
}

