/* eslint-disable */
import BigInteger from "big-integer";



function remZeros(value){
    let index = value.length -1;
    while(index > 0 && value[index] === '0') index--;
    return value.slice(0, index+1);
}

function sol(value: number|string) {
    let num = value.toString();
    const decimals = 9;
    if(num.length > 12){
        return supply(num.slice(0, num.length-9));
    }
    if(num === "0"){
        return "0";
    }
    if(num.length < (decimals+1)){
        const shift = (decimals+1) - num.length;
        const str = new Array(shift+1).join( '0' );
        num = str + num;
    }
    let complete = num.slice(0, -decimals);
    let fraction = remZeros(num.slice(-decimals))
    let index = 0;
    while((index < (fraction.length+1)) && (fraction[index]==='0')) index++;
    let end = index;
    for(let j = index; (j < fraction.length)&&(j < (index + 4)); j++) end++;
    fraction = fraction.slice(0, end);
    return complete + "," + fraction;
}

function supply(value: number|string) {
    value = value.toString();
    let index = 1;
    let postfix = "";
    while(value.length > index * 3) {index++; postfix += 'K';}
    return value.slice(0, value.length - (index-1)*3)+postfix;
}

function bp(value: number|string) {
    value = parseInt(value.toString());
    return (value/100).toString();
}

function convertFloat(n, decimals): BigInteger|undefined{
    const a = n.split(/(?:,|\.)/);
    let number;
    if (a.length === 1) {
        number = a + new Array(decimals+1).join('0');
    }
    else{
        if (a.length > 2) {
            return undefined;
        }
        if (a.length === 0) { // @ts-ignore
            return BigInteger("0");
        }
        let fraction = a.length > 1 ? a[1] : "0";
        let complete = a[0];
        if (fraction.length > decimals) {
            fraction = fraction.slice(0, decimals);
        }
        number = complete + fraction + new Array(decimals - fraction.length + 1).join('0');
    }
    try{// @ts-ignore
        return BigInteger(number);
    }
    catch (_e) {
        return undefined;
    }
}

function wallet(s: string): string {
    return s.slice(0, 4)+'...'+s.slice(-4);
}


export default {sol, supply, bp, convertFloat, wallet};