import Swal from "sweetalert2";


export async function showError(msg: string) {
    Swal.fire({
        position: 'top',
        imageHeight: 500,
        icon: 'error',
        title: msg,
        showConfirmButton: false,
        timer: 1000,
        background: "#1E2424",
        color: "#FFFDFD",
        iconColor: "#f18181"
    });
}
export async function showSuccess(msg: string) {
    Swal.fire({
        position: 'top',
        imageHeight: 500,
        icon: 'success',
        title: msg,
        showConfirmButton: false,
        timer: 1000,
        background: "#1E2424",
        color: "#FFFDFD",
        iconColor: "#2ED69B"
    });
}