
import {defineComponent} from "vue";
import CollectionRow from "@/components/Search/CollectionRow.vue";
export default defineComponent({
  name: "SearchPage",
  components: {CollectionRow},
  data() {
    return {
      get: global.get
    }
  },
  beforeMount() {
    global.updateList.add(this);
  },
  beforeUnmount() {
    global.updateList.delete(this);
  }
})
