
import {defineComponent} from "vue";
import {Page} from "@/methods/types";
import display from "@/methods/display";
export default defineComponent({
  name: "NavBar",
  props: ['wallet'],
  methods: {
    async connect(){
      this.wallet.getProvider();
    },
    redirectToIdle(){
      global.App.setPage(Page.Idle);
    },
    redirectToTop(){
      if(global.App.activePage!==Page.Search){
        global.App.setPage(Page.Search);
      }
    },
    redirectToPocket(){
      global.App.setPage(Page.Pocket);
    },
    search(value: string){
      this.bCollectionFound = false;
      this.searchInput = value;
      if(!value || value==="") {
        this.searchPostfix = "";
        return;
      }
      if(value.length < 3){
        this.searchPostfix = "";
        return;
      }
      const list = global.GI.GS.collectionList;
      const length = value.length;
      const found = list.find((currentValue) => {
        return currentValue.slice(0, length)===value;
      });
      if(!found){
        this.searchPostfix = "";
        return;
      }
      this.searchPostfix = found.slice(length, found.length);
      this.bCollectionFound = true;
    },
    onEnter(){
      if(this.bCollectionFound){
        global.App.setPage(Page.Collection, this.searchInput+this.searchPostfix);
        this.search("");
      }
    }
  },
  data(){
    return{
      Page: Page,
      display: display,
      searchInput: "" as string,
      searchPostfix: "" as string,
      bCollectionFound: false as boolean
    }
  }
});
