/* eslint-disable */
import {PublicKey} from "@solana/web3.js";
import env from "../env";


const SystemProgramId = new PublicKey("11111111111111111111111111111111");
const TokenProgram = new PublicKey("TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA");
const AssociatedProgram = new PublicKey("ATokenGPvbdGVxr1b2hvZbsiqW5xWH25efTNsLJA8knL");
const MetadataProgram = new PublicKey("metaqbxxUerdq28cj1RbAWkYQm3ybzjb6a8bt518x1s");
const Sysvar = new PublicKey("SysvarRent111111111111111111111111111111111");


export async function getMetadataAddress(mint: PublicKey): Promise<PublicKey>{
    const a = await PublicKey.findProgramAddress(
        [
            Buffer.from('metadata'),
            MetadataProgram.toBuffer(),
            mint.toBuffer(),
        ],
        MetadataProgram,
    );
    return a[0];
}

export async function getAssociatedAccount(owner: PublicKey, mint: PublicKey): Promise<PublicKey>{
    return (await PublicKey.findProgramAddress(
        [
            owner.toBuffer(),
            TokenProgram.toBuffer(),
            mint.toBuffer(),
        ],
        AssociatedProgram
    ))[0];
}

export async function getDeckTreasury(mint: PublicKey, deck: PublicKey) {
    const a = await PublicKey.findProgramAddress(
        [
            Buffer.from('treasury'),
            mint.toBuffer(),
            deck.toBuffer()
        ],
        env.MarketplaceProgram
    );
    return a[0];
}

export async function getDeckAddress(collection) {
    const a = await PublicKey.findProgramAddress(
        [
            Buffer.from('deck'),
            collection.toBuffer(),
        ],
        env.MarketplaceProgram
    );
    return a[0];
}

export async function getOfferAddress(mint) {
    const a = await PublicKey.findProgramAddress(
        [
            Buffer.from('offer'),
            mint.toBuffer(),
        ],
        env.MarketplaceProgram
    );
    return a[0];
}