
import {defineComponent} from "vue";
import RaffleCard from "@/components/Idle/RaffleCard.vue";
import {Page} from "@/methods/types";

export default defineComponent({
  name: 'IdlePage',
  components: {
    RaffleCard
  },
  methods: {
    redirect(){
      global.App.setPage(Page.Collection, this.get('GS.topCollections[0].name'));
    }
  },
  data() {
    return {
      get: global.get
    }
  },
  beforeMount() {
    global.updateList.add(this);
  },
  beforeUnmount() {
    global.updateList.delete(this);
  }
})
