/* eslint-disable */
import Socket from "@/methods/socket";
import {IDeck, IOffer, IPocket, Offer} from "@/methods/types";


export class UpdateManager {
    socket: Socket;
    pocketSubscriptions: Map<string, string[]> = new Map(); // <CollectionName, OfferAddresses[]>, undefined instead of empty Array!
    pocket: IPocket = {
        placed: new Map(),
        owned: new Map(),
        past: []
    }; //@ts-ignore
    activeDeck: IDeck = {name: undefined};
    deckOffers: Map<string, Offer> = new Map();
    constructor() {
        this.socket = new Socket();
    }
    force() {
        global.updateList.forEach((child) => {
            child.$forceUpdate();
        });
    }
// +++++++++++ Elements Watcher ++++++++++
    async watchPocket() {
        const watched: Set<string> = new Set(this.pocketSubscriptions.keys());
        if (this.activeDeck) watched.add(this.activeDeck.name);
        this.pocketSubscriptions = new Map();
        const addWatch: Set<string> = new Set();
        this.pocket.placed.forEach((offer, _offerAddress) => {
            const deckName = offer.deck_name;
            if (!this.pocketSubscriptions.has(deckName)) this.pocketSubscriptions.set(deckName, []);
            this.pocketSubscriptions.get(deckName).push(offer.address);
            if (!watched.has(deckName)) addWatch.add(deckName);
        });
        await this.socket.methods.subscribe(Array.from(addWatch) as string[]);
    }
    async watchOffer(offerAddress: string, deckName: string) {
        if (!this.pocketSubscriptions.has(deckName)) {
            this.pocketSubscriptions.set(deckName, []);
            await this.socket.methods.subscribe([deckName]);
        }
        this.pocketSubscriptions.get(deckName).push(offerAddress);
    }
    async unWatchOffer(offerAddress: string, deckName: string) {
        this.pocketSubscriptions.get(deckName).splice(this.pocketSubscriptions.get(deckName).indexOf(offerAddress), 1);
        if (this.pocketSubscriptions.get(deckName).length === 0) {
            this.pocketSubscriptions.set(deckName, []);
            await this.socket.methods.unSubscribe([deckName]);
        }
    }
    async setActiveDeck(name: string) {
        const res = await this.socket.methods.getDeck(name) as { Deck: IDeck, Offers: Offer[] };
        if (!res) {
            console.log('Failed to get Deck:', name);
            return;
        }
        this.activeDeck = res.Deck;
        res.Offers.forEach(offer => {
            if(!this.pocket.placed.has(offer.address)) this.deckOffers.set(offer.address, offer);
        });
        await this.socket.methods.subscribe([name]);
    }
    async flushDeck() {
        const name = this.activeDeck.name;
        if (this.pocketSubscriptions.has(name)) {
            if (this.pocketSubscriptions.get(name).length > 0) return;
        } //@ts-ignore
        this.activeDeck = {name: undefined};
        this.deckOffers = new Map();
        await this.socket.methods.unSubscribe([name]);
    }
// ++++++++++ Offer Update Manager ++++++++++
    pushOfferCreated(offerAddress: string, deckName: string, offer: IOffer) {
        if (this.activeDeck.name !== deckName) return;
        this.deckOffers.set(offerAddress, offer as Offer);
        this.force();
    }
    pushOfferCanceled(offerAddress: string, deckName: string) {
        if (this.activeDeck.name !== deckName) return;
        if (!this.deckOffers.has(offerAddress)) return;
        this.deckOffers.delete(offerAddress);
        this.force();
    }
    pushOfferAccepted(offerAddress: string, deckName: string) {
        if (this.pocket.placed.has(offerAddress)) {
            const offer = this.pocket.placed.get(offerAddress);
            this.pocket.placed.delete(offerAddress);
            this.pocket.past.unshift({
                deck_name: deckName,
                name: offer.name,
                price: parseInt(offer.price),
                time: 0
            });
        }
        if (this.activeDeck.name === deckName) {
            this.deckOffers.delete(offerAddress);
        }
        this.force();
    }
}