/* eslint-disable */
import {IGlobalState, IOffer, NFT, Offer, PastOffer} from "@/methods/types";
import LocalMemory from "@/methods/local-memory";
import {loadNFTCaches, parseOffer, searchNFTs} from "@/methods/parsers";
import {PublicKey} from "@solana/web3.js";
import {getAssociatedAccount, getOfferAddress} from "@/methods/accounts";
import {UpdateManager} from "@/methods/global/update-manager";


function getKeyByValue(object, value) {
    return Object.keys(object).find(key => object[key] === value);
}

export default class GlobalInstance extends UpdateManager{
    GS: IGlobalState;
    constructor(app) {
        super();
        this._setGlobal(app);
        this.GS = LocalMemory.loadGS();
        this._updateGS();
    }
    _setGlobal(app: any) {
        global.GI = this;
        global.updateList = new Set([app]);
        global.get = (val: string, def=undefined) => {
            try{
                return eval('global.GI.'+val);
            }
            catch (_e){
                return def;
            }
        }
        global.App = app;
    }
    async _updateGS() {
        const res = await this.socket.methods.getGlobal() as IGlobalState;
        if(!res) {
            console.log('Failed to get Global State');
            return;
        }
        this.GS = res;
        LocalMemory.setGS(res);
        this.force();
    }
    async switchDeck(name: string) {
        if(name===this.activeDeck.name) return;
        await this.flushDeck();
        if(name===undefined) return;
        await this.setActiveDeck(name);
        this.force();
    }
    async loadNFT(address: string) {
        if(!address) return;
        const offer = this.deckOffers.get(address);
        if(offer.cached) return;
        this.deckOffers.set(address, await loadNFTCaches(offer));
    }
// ++++++++++ OfferRegistration ++++++++++
    async offerCanceled(offer: Offer, tx: string) {
        await this.socket.methods.cancelOffer(offer.address, tx);
        await this.unWatchOffer(offer.address, offer.deck_name);
        this.pocket.placed.delete(offer.address);
        const offerParsed = await parseOffer(new PublicKey(offer.address));
        const deckIndex = this.GS.collectionList.indexOf(offer.deck_name);
        const collectionAddress: string = getKeyByValue(this.GS.collectionAddress, deckIndex);
        const nft: NFT = {
            mint: offerParsed.nft_mint,
            collectionAddress: new PublicKey(collectionAddress),
            account: await getAssociatedAccount(global.wallet.publicKey, offerParsed.nft_mint),
            collectionName: offer.deck_name,
            name: offer.name,
            image: offer.image,
            cached: undefined
        };
        this.pocket.owned.set(nft.mint.toBase58(), nft);
        this.force();
        LocalMemory.setPocket(this.pocket);
    }
    async offerPlaced(nft: NFT, price: BigInteger, tx: string) {
        const offerAddress = await getOfferAddress(nft.mint);
        const offer: Offer = {
            address: offerAddress.toBase58(),
            deck_name: nft.collectionName,
            name: nft.name,
            image: nft.image,
            price: price.toString(),
            seller: global.wallet.publicKey.toBase58(),
            cached: undefined
        };
        await this.socket.methods.createOffer(offer.seller, offer.deck_name, offer.address,
            offer.name, offer.image, offer.price, tx);
        await this.watchOffer(offer.address, nft.collectionName);
        this.pocket.owned.delete(nft.mint.toBase58());
        this.pocket.placed.set(offer.address, offer);
        this.force();
        LocalMemory.setPocket(this.pocket);
    }
    async offerAccepted(offer: Offer, tx: string) {
        await this.socket.methods.acceptOffer(global.wallet.publicKey.toBase58(), offer.address, tx);
        const offerParsed = await parseOffer(new PublicKey(offer.address));
        const deckIndex = this.GS.collectionList.indexOf(offer.deck_name);
        const collectionAddress: string = getKeyByValue(this.GS.collectionAddress, deckIndex);
        const nft: NFT = {
            mint: offerParsed.nft_mint,
            collectionAddress: new PublicKey(collectionAddress),
            account: await getAssociatedAccount(global.wallet.publicKey, offerParsed.nft_mint),
            collectionName: offer.deck_name,
            name: offer.name,
            image: offer.image,
            cached: undefined
        };
        this.pocket.owned.set(nft.mint.toBase58(), nft);
        this.force();
        LocalMemory.setPocket(this.pocket);
    }
// ++++++++++ Pocket Manager ++++++++++
    restorePocket() {
        this.pocket = LocalMemory.loadPocket();
        this.force();
    }
    async updatePocket() {
        const res = await this.socket.methods.getUserOffers(global.wallet.publicKey.toBase58()) as IOffer[];
        if(!res) {
            console.log('Failed to get User Offers:', global.wallet.publicKey.toBase58());
            return;
        }
        const placedNFTs: Set<string> = new Set();
        res.forEach(offer => {
            this.pocket.placed.set(offer.address, offer as Offer);
            placedNFTs.add(offer.image);
        });
        const res2 = await this.socket.methods.getPastOffers(global.wallet.publicKey.toBase58()) as PastOffer[];
        if(!res2) {
            console.log('Failed to get User Past Offers:', global.wallet.publicKey.toBase58());
            return;
        }
        this.pocket.past = res2.sort((a, b) => {return b.time - a.time});
        LocalMemory.setPocket(this.pocket);
        this.force();
        await this.watchPocket();
        const foundNFTs = await searchNFTs(global.wallet.publicKey, new Set(Object.keys(this.GS.collectionAddress)));
        this.pocket.owned = new Map([...foundNFTs].filter(([_k, v]) => {return !placedNFTs.has(v.image)})); // Filter by image url
        LocalMemory.setPocket(this.pocket);
        this.force();
    }
}